import Vue from 'vue'

exports.extend = function (target, source) {
    Object.assign(target, source);
    return target;
};

exports.isEqual = function (obj, target) {
    return Object.is(obj, target);
};

exports.byteLength = function (str) {
    return str.replace(/[^\x00-\xff]/g, 'xx').length;
};

exports.wrapper = function (Comp) {
    if (Vue.version.charAt(0) > 1) {
        return function (option) {
            option = option || {}
            var dom = document.createElement('div')
            dom.class = 'dialog-instance'
            dom.id = 'dialog' + new Date().getTime()
            let container = document.body
            if (option.container) {
                container = option.container
                if ('string' === typeof option.container) {
                    container = document.querySelector(option.container)
                }
            }
            container.appendChild(dom)
            option.extends = Comp
            var inst = new Vue(option)
            inst.$mount(dom)
            return inst
        }
    }
    else if (Vue.extend) {
        return Vue.extend({
            mixins: [Comp],
            el() {
                var dom = document.createElement('div');
                dom.class = 'dialog-instance';
                document.body.appendChild(dom);
                return dom;
            }
        })
    }
}


